import React, { useContext, useEffect, useState } from "react";
import DataTable from "react-data-table-component";
import "./tabla.css";

import ModalRegistroPersonal from "../personal/trabajadores/ModalRegistroPersonal";
import { CrudContext } from "../../context/CrudContext";
import ModalHistorialEvaluacion from "../personal/trabajadores/ModalHistorialEvaluacion";
import { Empty } from "antd";
import TablaAsociacionTrabajador from "./TablaAsociacionTrabajador";

const Tabla = ({ columns, table, actualizarTabla, filterText }) => {
  const { setModal, modal } = useContext(CrudContext);
  const [id, setId] = useState("");
  const [modalHistorial, setModalHistorial] = useState(false);
  const paginationComponentOptions = {
    rowsPerPageText: "Filas por página",
    rangeSeparatorText: "de",
    selectAllRowsItem: true,
    selectAllRowsItemText: "Todos",
  };

  const conditionalRowStyles = [
    {
      when: (row) => row?.contrato,
      style: (row) => ({
        backgroundColor: row?.contrato?.length > 0 ? "#87b07bf6" : "",
        fontSize: "15px",
      }),
    },
  ];

  const expandedComponent = ({ data }) => (
    <div style={{ padding: "5px 5px 5px 5px", backgroundColor: "#DFE4E4" }}>
      <TablaAsociacionTrabajador
        data={data}
        setModalHistorial={setModalHistorial}
        setId={setId}
        actualizarTabla={actualizarTabla}
      />
    </div>
  );
  return (
    <div className="table-container">
      <DataTable
        columns={columns}
        data={table}
        pagination
        fixedHeader
        striped
        expandableRows
        expandableRowsComponent={expandedComponent}
        expandableRowDisabled={(row) =>
          row?.trabajador?.length === 0 ? true : false
        }
        paginationComponentOptions={paginationComponentOptions}
        responsive
        noHeader={true}
        conditionalRowStyles={conditionalRowStyles}
        noDataComponent={
          <Empty
            image={Empty.PRESENTED_IMAGE_SIMPLE}
            description={<span>No hay registros para mostrar.</span>}
          />
        }
      />

      {modal && (
        <ModalRegistroPersonal
          actualizarTabla={actualizarTabla}
          modal={modal}
          setModal={setModal}
        />
      )}
      {modalHistorial && (
        <ModalHistorialEvaluacion
          selected={id}
          actualizarTrabajador={actualizarTabla}
          modal1={modalHistorial}
          setModal1={setModalHistorial}
        />
      )}
    </div>
  );
};

export default Tabla;
