import { Empty, Image, Popconfirm } from "antd";
import React from "react";
import { useState } from "react";
import { useContext } from "react";
import DataTable from "react-data-table-component";
import { AiFillEye, AiOutlineCheck, AiOutlineClose } from "react-icons/ai";
import { BsPencil, BsTrash } from "react-icons/bs";
import { CrudContext } from "../../context/CrudContext";
import { notificacion } from "../../helpers/mensajes";

const TablaAsociacionTrabajador = ({
  data,
  setModalHistorial,
  setId,
  actualizarTabla,
}) => {
  const { setDataToEdit, setModal, deleteData, currentPage, setCurrentPage } =
    useContext(CrudContext);
  // const [currentPage, setCurrentPage] = useState(1);
  const personal = [
    {
      id: "nro",
      name: "Nro",
      sortable: true,
      center: true,
      width: "80px",
      selector: (row) => row?.nro,
    },
    {
      id: "codigo",
      name: "Código",
      sortable: true,
      center: true,
      width: "140px",
      selector: (row) => row?.codigo_trabajador,
    },
    {
      id: "foto",
      name: "Foto",
      center: true,
      width: "120px",

      selector: (row) => (
        <div style={{ padding: "8px" }}>
          <Image
            src={row?.foto || "https://via.placeholder.com/60"}
            style={{
              height: "60px",
              width: "60px",
              borderRadius: "10%",
            }}
          />
        </div>
      ),
    },
    {
      id: "Trabajador",
      name: "Apellidos y Nombres",
      selector: (row) =>
        row?.apellido_paterno + " " + row?.apellido_materno + " " + row?.nombre,
      width: "300px",
      sortable: true,
    },
    {
      id: "Campamento",
      name: "Campamento",
      selector: (row) => (row?.campamento ? row?.campamento : "Por asignar"),
      sortable: true,
    },
    {
      id: "Dni",
      name: "Dni",
      selector: (row) => row?.dni,
      sortable: true,
    },
    {
      id: "telefono",
      name: "Teléfono",
      selector: (row) => row?.telefono,
      sortable: true,
    },

    {
      id: "Evaluación",
      name: "Evaluación",
      selector: (row) => row?.id,

      button: true,
      cell: (e, index) => (
        <>
          <AiFillEye
            style={{ cursor: "pointer" }}
            onClick={() => handleEvaluacion(e)}
          />
          {e?.evaluacions?.at(-1)?.fiscalizador_aprobado === "si" &&
          e?.evaluacions?.at(-1)?.control === "si" &&
          e?.evaluacions?.at(-1)?.topico === "si" &&
          e?.evaluacions?.at(-1)?.seguridad === "si" &&
          e?.evaluacions?.at(-1)?.medio_ambiente === "si" &&
          e?.evaluacions?.at(-1)?.recursos_humanos === "si" &&
          !e?.evaluacions?.at(-1)?.finalizado ? (
            <AiOutlineCheck
              style={{ color: "green", fontWeigth: "bold", fontSize: "16px" }}
            />
          ) : e?.evaluacions?.at(-1)?.id &&
            !e?.evaluacions?.at(-1)?.finalizado ? (
            <AiOutlineClose
              style={{ color: "red", fontWeigth: "bold", fontSize: "16px" }}
            />
          ) : (
            ""
          )}
        </>
      ),
    },

    {
      id: "Acciones",
      name: "Acciones",
      button: true,
      cell: (e) => (
        <div className="acciones">
          <BsPencil onClick={() => handleEdit(e)} />
          <Popconfirm
            title="Eliminar trabajador"
            description="¿Estas seguro de eliminar?"
            onConfirm={() => handleDelete(e.dni)}
            // onCancel={cancel}
            okText="Si"
            cancelText="No"
            placement="topRight"
          >
            <BsTrash />
          </Popconfirm>
        </div>
      ),
    },
  ];

  const handleEdit = (e) => {
    setDataToEdit(e);
    setModal(true);
  };
  const handleDelete = async (e) => {
    const response = await deleteData("trabajador", e);
    if (response) {
      notificacion(response.status, response.msg);
      actualizarTabla();
    }
  };

  const paginationComponentOptions = {
    rowsPerPageText: "Filas por página",
    rangeSeparatorText: "de",
    selectAllRowsItem: true,
    selectAllRowsItemText: "Todos",
  };
  const handlePageChange = (page) => {
    // console.log("====================================");
    // console.log(page);
    // console.log("====================================");
    setCurrentPage(page);
  };
  const handleEvaluacion = (e) => {
    setModalHistorial(true);
    setId(e);
  };
  return (
    <div>
      <DataTable
        columns={personal}
        data={data.trabajador}
        // pagination
        // paginatioCurrentPage={currentPage}
        // paginationPerPage={5}
        // onChangePage={(page) => {
        //   console.log("onChangePage fired with page:", page);
        //   handlePageChange(page);
        // }}
        noDataComponent={
          <Empty
            image={Empty.PRESENTED_IMAGE_SIMPLE}
            description={<span>No hay registros para mostrar.</span>}
          />
        }
        paginationComponentOptions={paginationComponentOptions}
        paginationRowsPerPageOptions={[5, 10, 15, 20]}
      />
    </div>
  );
};

export default TablaAsociacionTrabajador;
